
ul, li {
  list-style: none;
  padding: 0;
  margin: 0;
}
.col-wrapper {
  position: relative;
  border: 1px solid #CCC;
  height: 300px;
  overflow: hidden;
  text-align: center;
}
.col-wrapper .wheel-list {
    position: absolute;
    width: 100%;
    transform-style: preserve-3d;
}
.col-wrapper .wheel-list .wheel-item {
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
}
.col-wrapper .cover {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6)), linear-gradient(0deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
    background-position: top,bottom;
    background-repeat: no-repeat;
}
.col-wrapper .divider {
    position: absolute;
    width: 100%;
    left: 0;
    border-top: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
}



